<template>
  <n-drawer
    v-bind:show="drawerActive"
    @update:show="saveConfigToCookie"
    :height="275"
    :width="251"
    :placement="drawerPlacement"
  >
    <n-drawer-content closable>
      <!--  -->
      <template #header>
        <span>{{ $t('ReaderSettings') }}</span>
      </template>
      <!--  -->
      <n-space>
        <n-radio-group v-model:value="nowTemplateLocal">
          <!--  -->
          <n-radio-button
            :checked="nowTemplateLocal === 'scroll'"
            @change="onChangeTemplate"
            value="scroll"
            name="basic-demo"
          >{{ $t('scroll_mode') }}</n-radio-button>
          <!--  -->
          <n-radio-button
            :checked="nowTemplateLocal === 'flip'"
            @change="onChangeTemplate"
            value="flip"
            name="basic-demo"
          >{{ $t('flip_mode') }}</n-radio-button>
        </n-radio-group>
      </n-space>
      <!--  -->
      <n-divider />
      <!--  -->
      <slot></slot>
      <n-divider />
      <n-popconfirm @positive-click="handlePositiveClick" @negative-click="handleNegativeClick">
        <template #trigger>
          <n-button>{{ $t('reset_all_settings') }}</n-button>
        </template>
        {{ $t('do_you_reset_all_settings') }}
      </n-popconfirm>
      <!--  -->
      <template #footer>
        <n-select
          placeholder="select language"
          v-model:value="this.$i18n.locale"
          :options="this.languageOptions"
          @update:value="OnChangeLanguage"
        />
        <n-button
          v-if="nowTemplate == 'flip' || nowTemplate == 'scroll'"
          @click="startSketchMode"
        >{{ $t('startSketchMode') }}</n-button>
        <n-button v-if="nowTemplate == 'sketch'" @click="stopSketchMode">{{ $t('stopSketchMode') }}</n-button>
      </template>
    </n-drawer-content>
  </n-drawer>
</template>

<script>

import { useCookies } from "vue3-cookies";
import { NDrawer, NDivider, NDrawerContent, NSpace, NRadioGroup, NRadioButton, NButton, NSelect, NPopconfirm, } from 'naive-ui'
import { defineComponent, } from 'vue'
// import { useI18n } from 'vue-i18n'


export default defineComponent({
  name: "Drawer",
  props: ['book', 'initDrawerActive', 'initDrawerPlacement', 'nowTemplate'],
  emits: ["setT", "saveConfig", "startSketch", "stopSketch", "closeDrawer"],
  components: {
    NDrawer,//4. https://www.naiveui.com/zh-CN/os-theme/components/drawer
    NDrawerContent,//
    NSpace,// https://www.naiveui.com/zh-CN/os-theme/components/space
    NDivider,//
    NRadioGroup,//  https://www.naiveui.com/zh-CN/os-theme/components/radio
    NRadioButton,// 
    NButton,//:https://www.naiveui.com/zh-CN/os-theme/components/button
    // NAvatar,// https://www.naiveui.com/zh-CN/os-theme/components/avatar
    NSelect, // https://www.naiveui.com/zh-CN/os-theme/components/select
    NPopconfirm, // https://www.naiveui.com/zh-CN/os-theme/components/popconfirm
  },
  setup() {
    const { cookies } = useCookies();
    // const message = useMessage();  'naive-ui'useMessage

    return {
      handlePositiveClick() {
        // message.info("");
        //localStorage
        localStorage.clear();
        //
        location.reload();
      },
      handleNegativeClick() {
        // message.info("");
      },
      cookies,
      languageOptions: [
        {
          label: "English",
          value: 'en',
        },
        {
          label: '',
          value: 'ja'
        },
        {
          label: '',
          value: 'zh'
        },
      ],
    };
  },
  data() {
    return {
      someflag: "",
      nowTemplateLocal: "",
    };
  },
  //
  beforeMount() {
    var lang = this.cookies.get("userLanguageSetting");
    if (lang) {
      this.$i18n.locale = lang;
    }
    this.nowTemplateLocal = this.nowTemplate;
  },
  computed: {
    drawerActive() {
      return this.initDrawerActive;
    },
    drawerPlacement() {
      return this.initDrawerPlacement;
    },
  },
  methods: {

    onClearLocalSetting() {
      // //localStorage
      // localStorage.clear();
      // //
      // location.reload();
      //
      //window.location.replace("http://www.example.com")
    },

    OnChangeLanguage(value) {
      this.cookies.set("userLanguageSetting", value);
    },
    // cookies
    saveConfigToCookie(show) {
      if (show == false) {
        this.$emit('closeDrawer');
        this.$emit('saveConfig');
      }
    },
    startSketchMode() {
      this.$emit('startSketch');
    },
    stopSketchMode() {
      this.$emit('stopSketch');
    },
    //vue-router
    onChangeTemplate() {
      // this.$emit("greet", this.nowTemplateLocal);
      // console.log("onChangeTemplate:"+value);
      if (this.nowTemplateLocal === "scroll") {
        this.$emit("setT", "scroll");
      }
      if (this.nowTemplateLocal === "flip") {
        this.$emit("setT", "flip");
      }
      if (this.nowTemplateLocal === "sketch") {
        this.$emit("setT", "sketch");
      }
      // location.reload(); // VueRouter
    },
  },
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>




