<template>
	<div id="ScrollMode" v-if="this.book" class="manga">
		<Header
			class="footer"
			v-if="this.showHeaderFlag"
			:bookIsFolder="book.IsFolder"
			:bookName="book.name"
		>
			<!--  -->
			<n-icon size="40" @click="drawerActivate('right')">
				<settings-outline />
			</n-icon>
		</Header>

		<!--  -->
		<div
			class="main_manga"
			v-for="(page, key) in book.pages"
			:key="page.url"
			@click="onMouseClick($event)"
			@mousemove="onMouseMove"
			@mouseleave="onMouseLeave"
		>
			<img v-lazy="imageParametersString(page.url)" v-bind:alt="key + 1" v-bind:key="key" />
			<div class="page_hint" v-if="showPageNumFlag_ScrollMode">{{ key + 1 }}/{{ book.all_page_num }}</div>
		</div>

		<Drawer
			:initDrawerActive="this.drawerActive"
			:initDrawerPlacement="this.drawerPlacement"
			@saveConfig="this.saveConfigToLocalStorage"
			@startSketch="this.startSketchMode"
			@closeDrawer="this.drawerDeactivate"
			@setT="this.OnSetTemplate"
			:nowTemplate="this.nowTemplate"
		>
			<span>{{ $t("setBackColor") }}</span>
			<n-color-picker
				v-model:value="model.color"
				:modes="['rgb']"
				:show-alpha="false"
				@update:value="onBackgroundColorChange"
			/>

			<!--  -->
			<n-divider />

			<n-space vertical>
				<!-- -- -->
				<!-- % -->
				<n-input-number
					v-if="this.imageWidth_usePercentFlag"
					size="small"
					:show-button="false"
					v-model:value="this.singlePageWidth_Percent"
					:max="100"
					:min="10"
					:update-value-on-input="false"
				>
					<template #prefix>{{ $t('singlePageWidth') }}</template>
					<template #suffix>%</template>
				</n-input-number>
				<!-- % -->
				<n-slider
					v-if="this.imageWidth_usePercentFlag"
					v-model:value="this.singlePageWidth_Percent"
					:step="1"
					:max="100"
					:min="10"
					:format-tooltip="value => `${value}%`"
				/>

				<!-- --  -->
				<!-- % -->
				<n-input-number
					v-if="this.imageWidth_usePercentFlag"
					size="small"
					:show-button="false"
					v-model:value="this.doublePageWidth_Percent"
					:max="100"
					:min="10"
					:update-value-on-input="false"
				>
					<template #prefix>{{ $t('doublePageWidth') }}</template>
					<template #suffix>%</template>
				</n-input-number>
				<!-- % -->
				<n-slider
					v-if="this.imageWidth_usePercentFlag"
					v-model:value="this.doublePageWidth_Percent"
					:step="1"
					:max="100"
					:min="10"
					:format-tooltip="value => `${value}%`"
				/>

				<!-- --PX -->

				<!--  -->
				<n-input-number
					v-if="!this.imageWidth_usePercentFlag"
					size="small"
					:show-button="false"
					v-model:value="this.singlePageWidth_PX"
					:max="this.imageMaxWidth"
					:min="50"
					:update-value-on-input="false"
				>
					<template #prefix>{{ $t('singlePageWidth') }}</template>
					<template #suffix>px</template>
				</n-input-number>
				<!-- PX -->
				<n-slider
					v-if="!this.imageWidth_usePercentFlag"
					v-model:value="this.singlePageWidth_PX"
					:step="10"
					:max="this.imageMaxWidth"
					:min="50"
					:format-tooltip="value => `${value}px`"
				/>

				<!--  -->
				<n-input-number
					v-if="!this.imageWidth_usePercentFlag"
					size="small"
					:show-button="false"
					v-model:value="this.doublePageWidth_PX"
					:max="this.imageMaxWidth"
					:min="50"
					:update-value-on-input="false"
				>
					<template #prefix>{{ $t('doublePageWidth') }}</template>
					<template #suffix>px</template>
				</n-input-number>

				<!-- PX -->
				<n-slider
					v-if="!this.imageWidth_usePercentFlag"
					v-model:value="this.doublePageWidth_PX"
					:step="10"
					:max="this.imageMaxWidth"
					:min="50"
					:format-tooltip="value => `${value}px`"
				/>
			</n-space>

			<p></p>
			<!--  -->
			<n-space>
				<n-switch
					size="large"
					v-model:value="this.imageWidth_usePercentFlag"
					:rail-style="railStyle"
					@update:value="this.setImageWidthUsePercentFlag"
				>
					<template #checked>{{ $t('width_usePercent') }}</template>
					<template #unchecked>{{ $t('width_useFixedValue') }}</template>
				</n-switch>
			</n-space>

			<!--  -->
			<n-divider />

			<!--  -->
			<n-space>
				<n-switch size="large" v-model:value="this.showHeaderFlag" @update:value="setShowHeaderChange">
					<template #checked>{{ $t('showHeader') }}</template>
					<template #unchecked>{{ $t('showHeader') }}</template>
				</n-switch>
				<p></p>
			</n-space>

			<!--  -->
			<n-space>
				<n-switch
					size="large"
					v-model:value="this.showPageNumFlag_ScrollMode"
					@update:value="setShowPageNumChange"
				>
					<template #checked>{{ $t('showPageNum') }}</template>
					<template #unchecked>{{ $t('showPageNum') }}</template>
				</n-switch>
			</n-space>

			<!--   -->
			<n-space>
				<n-switch
					size="large"
					v-model:value="this.imageParameters.gray"
					@update:value="setImageParameters_Gray"
				>
					<template #checked>{{ $t('gray_image') }}</template>
					<template #unchecked>{{ $t('gray_image') }}</template>
				</n-switch>
			</n-space>
			<!--  -->
			<n-divider />
			<!--  -->
			<n-space>
				<n-switch
					size="large"
					v-model:value="this.imageParameters.do_auto_crop"
					@update:value="setImageParameters_DoAutoCrop"
				>
					<template #checked>{{ $t('auto_crop') }}</template>
					<template #unchecked>{{ $t('auto_crop') }}</template>
				</n-switch>
				<!--  -->
				<n-input-number
					:show-button="false"
					v-if="this.imageParameters.do_auto_crop"
					v-model:value="this.imageParameters.auto_crop"
					:max="10"
					:min="0"
				></n-input-number>
			</n-space>
			<!--  -->
			<n-space>
				<n-switch
					size="large"
					:rail-style="railStyle"
					v-model:value="this.imageParameters.do_auto_resize"
					@update:value="setImageParameters_DoAutoResize"
				>
					<template #checked>{{ $t('image_width_limit') }}</template>
					<template #unchecked>{{ $t('raw_resolution') }}</template>
				</n-switch>
				<!--  -->
				<n-input-number
					v-if="this.imageParameters.do_auto_resize"
					size="small"
					:show-button="false"
					v-model:value="this.imageParameters.resize_max_width"
					:max="this.imageMaxWidth"
					:min="100"
				>
					<template #prefix>{{ $t('max_width') }}</template>
					<template #suffix>px</template>
				</n-input-number>
			</n-space>
		</Drawer>

		<n-back-top :show="showBackTopFlag" type="info" color="#8a2be2" :right="20" :bottom="20" />
		<n-button @click="scrollToTop(90);" size="large" secondary strong>{{ $t('back-to-top') }}</n-button>
	</div>
</template>

<script>
// 
import { NButton, NBackTop, NSpace, NSlider, NSwitch, NIcon, NInputNumber, NDivider, NColorPicker, } from 'naive-ui'
import Header from "@/components/Header.vue";
import Drawer from "@/components/Drawer.vue";
import { defineComponent, reactive } from 'vue'
import { useCookies } from "vue3-cookies";// https://github.com/KanHarI/vue3-cookies
import { SettingsOutline } from '@vicons/ionicons5'

export default defineComponent({
	name: "ScrollMode",
	props: ['book', 'nowTemplate'],
	emits: ["setTemplate"],
	components: {
		Header,//
		Drawer,//
		NButton,//:https://www.naiveui.com/zh-CN/os-theme/components/button
		NBackTop,//:https://www.naiveui.com/zh-CN/os-theme/components/back-top
		// NDrawer,//4. https://www.naiveui.com/zh-CN/os-theme/components/drawer
		// NDrawerContent,//
		NSpace,// https://www.naiveui.com/zh-CN/os-theme/components/space
		// NRadio,//  https://www.naiveui.com/zh-CN/os-theme/components/radio
		// NRadioButton,//  
		// NRadioGroup,
		NSlider,//  Slider https://www.naiveui.com/zh-CN/os-theme/components/slider
		NSwitch,//   https://www.naiveui.com/zh-CN/os-theme/components/switch
		// NLayout,// https://www.naiveui.com/zh-CN/os-theme/components/layout
		// NLayoutSider,
		// NLayoutContent,
		NIcon,//  https://www.naiveui.com/zh-CN/os-theme/components/icon
		// NPageHeader,// https://www.naiveui.com/zh-CN/os-theme/components/page-header
		// NAvatar, // https://www.naiveui.com/zh-CN/os-theme/components/avatar
		NInputNumber,// https://www.naiveui.com/zh-CN/os-theme/components/input-number
		SettingsOutline,//, https://www.xicons.org/#/   npm i -D @vicons/ionicons5
		NDivider,//  https://www.naiveui.com/zh-CN/os-theme/components/divider
		NColorPicker,
	},
	setup() {
		//this
		const { cookies } = useCookies();
		//
		//reactive({}) : https://www.bilibili.com/video/av925511720/?p=4  toRefs()
		const model = reactive({
			color: "#f6f7eb",
			colorHeader: "#d1c9c1",
		});

		const imageParameters = reactive({
			resize_width: -1,// 
			resize_height: -1,// 
			do_auto_resize: false,
			resize_max_width: 800,// 
			resize_max_height: -1,//
			do_auto_crop: false,
			auto_crop: 1,// 0~100,1	
			gray: false,//
		});

		//,ref
		// const checkedValueRef = ref(null)
		return {
			cookies,
			//
			model,
			imageParameters,//
			imageParametersString: (source_url) => {
				// var temp =
				if (source_url.substr(0, 12) == "api/getfile?") {
					return source_url + "&resize_width=" + imageParameters.resize_width + "&resize_height=" + imageParameters.resize_height + "&resize_max_width=" + (imageParameters.do_auto_resize ? imageParameters.resize_max_width : -1) + "&resize_max_height=" + imageParameters.resize_max_height + "&auto_crop=" + (imageParameters.do_auto_crop ? imageParameters.auto_crop : -1) + "&gray=" + (imageParameters.gray ? 'true' : 'false')
				} else {
					return source_url
				}

			},
			//
			railStyle: ({ focused, checked }) => {
				const style = {}
				if (checked) {
					style.background = '#d03050'
					if (focused) {
						style.boxShadow = '0 0 0 2px #d0305040'
					}
				} else {
					style.background = '#2080f0'
					if (focused) {
						style.boxShadow = '0 0 0 2px #2080f040'
					}
				}
				return style
			},
			//
			marks: {
				30: '25%',
				50: '50%',
				75: '75%',
				95: '95%',
			},
		}
	},
	data() {
		return {
			drawerActive: false,
			drawerPlacement: 'right',
			// Debug
			debugModeFlag: true,
			//
			//
			showHeaderFlag: true,
			//
			showPageNumFlag_ScrollMode: false,
			//
			showBackTopFlag: false,
			//
			scrollDownFlag: false,
			//
			scrollTopSave: 0,
			//
			syncScrollFlag: false,
			//
			clickX: 0,
			clickY: 0,
			//
			isLandscapeMode: true,
			isPortraitMode: false,
			imageMaxWidth: 10,
			//inLandscapeMode
			aspectRatio: 1.2,

			// CSS!!!!!
			// https://v3.cn.vuejs.org/api/sfc-style.html#%E7%8A%B6%E6%80%81%E9%A9%B1%E5%8A%A8%E7%9A%84%E5%8A%A8%E6%80%81-css
			//
			imageWidth_usePercentFlag: false,

			//(Landscape)
			singlePageWidth_Percent: 50,
			doublePageWidth_Percent: 95,

			//(Landscape)PX
			singlePageWidth_PX: 720,
			doublePageWidth_PX: 1080,

			//
			clientWidth: 0,
			clientHeight: 0,
		};
	},
	//Vue3:  https://v3.cn.vuejs.org/api/options-lifecycle-hooks.html#beforecreate
	// created : 
	// beforeMount : 
	// mounted : 
	// beforeUpdate:  VNode 
	// updated:  VNode  VNode 
	// beforeUnmount: 
	// unmounted: 

	created() {
		window.addEventListener("scroll", this.onScroll);
		// resize  https://developer.mozilla.org/zh-CN/docs/Web/API/Window/resize_event
		window.addEventListener("resize", this.onResize);
		this.imageMaxWidth = window.innerWidth;
		//cookie,cookie,cookie

		//
		if (localStorage.getItem("showHeaderFlag") === "true") {
			this.showHeaderFlag = true;
		} else if (localStorage.getItem("showHeaderFlag") === "false") {
			this.showHeaderFlag = false;
		}
		//console.log(": showHeaderFlag=" + this.showHeaderFlag);

		//
		if (localStorage.getItem("showPageNumFlag_ScrollMode") === "true") {
			this.showPageNumFlag_ScrollMode = true;
		} else if (localStorage.getItem("showPageNumFlag_ScrollMode") === "false") {
			this.showPageNumFlag_ScrollMode = false;
		}
		//console.log(": showPageNumFlag_ScrollMode=" + this.showPageNumFlag_ScrollMode);

		//
		if (localStorage.getItem("ImageParameters_Gray") === "true") {
			this.imageParameters.gray = true;
		} else if (localStorage.getItem("ImageParameters_Gray") === "false") {
			this.imageParameters.gray = false;
		}
		// console.log(": ImageParameters_Gray=" + this.imageParameters.gray);

		//
		if (localStorage.getItem("ImageParameters_DoAutoResize") === "true") {
			this.imageParameters.do_auto_resize = true;
		} else if (localStorage.getItem("ImageParameters_DoAutoResize") === "false") {
			this.imageParameters.do_auto_resize = false;
		}

		//Width
		if (localStorage.getItem("ImageParametersResizeMaxWidth") != null) {
			let saveNum = Number(localStorage.getItem("ImageParametersResizeMaxWidth"));
			if (!isNaN(saveNum)) {
				this.imageParameters.resize_max_width = saveNum;
			}
		}

		//
		if (localStorage.getItem("ImageParameters_DoAutoCrop") === "true") {
			this.imageParameters.do_auto_crop = true;
		} else if (localStorage.getItem("ImageParameters_DoAutoCrop") === "false") {
			this.imageParameters.do_auto_crop = false;
		}

		//
		if (localStorage.getItem("ImageParameters_DoAutoCrop") != null) {
			let saveNum = Number(localStorage.getItem("ImageParameters_DoAutoCrop"));
			if (!isNaN(saveNum)) {
				this.imageParameters.auto_crop = saveNum;
			}
		}

		//
		if (localStorage.getItem("imageWidth_usePercentFlag") === "true") {
			this.imageWidth_usePercentFlag = true;
		} else if (localStorage.getItem("imageWidth_usePercentFlag") === "false") {
			this.imageWidth_usePercentFlag = false;
		}

		//javascript https://www.runoob.com/js/js-type-conversion.html
		// NaN==  ===

		//Percent
		if (localStorage.getItem("singlePageWidth_Percent") != null) {
			let saveNum = Number(localStorage.getItem("singlePageWidth_Percent"));
			if (!isNaN(saveNum)) {
				this.singlePageWidth_Percent = saveNum;
			}
		}

		if (localStorage.getItem("doublePageWidth_Percent") != null) {
			let saveNum = Number(localStorage.getItem("doublePageWidth_Percent"));
			if (!isNaN(saveNum)) {
				this.doublePageWidth_Percent = saveNum;
			}
		}

		//PX
		if (localStorage.getItem("singlePageWidth_PX") != null) {
			let saveNum = Number(localStorage.getItem("singlePageWidth_PX"));
			if (!isNaN(saveNum)) {
				this.singlePageWidth_PX = saveNum;
			}
		}
		if (localStorage.getItem("doublePageWidth_PX") != null) {
			let saveNum = Number(localStorage.getItem("doublePageWidth_PX"));
			if (!isNaN(saveNum)) {
				this.doublePageWidth_PX = saveNum;
			}
		}

		//
		if (localStorage.getItem("ScrollModeDefaultColor") != null) {
			this.model.color = localStorage.getItem("ScrollModeDefaultColor");
			this.onBackgroundColorChange(this.model.color);
		}

	},

	// //
	beforeMount() {

	},
	onMounted() {
		//console.log('mounted in the composition api!')
		this.isLandscapeMode = this.inLandscapeModeCheck();
		this.isPortraitMode = !this.inLandscapeModeCheck();
		// https://v3.cn.vuejs.org/api/options-lifecycle-hooks.html#beforemount
		this.$nextTick(function () {
			//
		})
	},
	//
	beforeUnmount() {
		//
		window.removeEventListener("scroll", this.onScroll);
		window.removeEventListener('resize', this.onResize)
	},
	methods: {
		//
		drawerActivate(place) {
			this.drawerActive = true
			this.drawerPlacement = place
		},
		//
		drawerDeactivate() {
			this.drawerActive = false
		},
		//
		startSketchMode() {
			this.$emit("setTemplate", "sketch");
		},
		//Draw
		OnSetTemplate(value) {
			this.$emit("setTemplate", value);
		},
		onBackgroundColorChange(value) {
			// value #997E50
			// 1610
			let r = Number('0x' + value.substr(1, 2))
			let g = Number('0x' + value.substr(3, 2))
			let b = Number('0x' + value.substr(5, 2))
			// console.log(value);
			// console.log("R:" + r + " G:" + g + " B:" + b);
			//
			let subR = 40
			let subG = 30
			let subB = 42
			let r2 = (r < 255 - subR) ? (r + subR) : (r + parseInt((255 - r) / 2))
			let g2 = (g < 255 - subG) ? (g + subG) : (g + parseInt((255 - g) / 2))
			let b2 = (b < 255 - subB) ? (b + subB) : (b + parseInt((255 - b) / 2))
			if (r > 250) {
				r2 = r - subR
			}
			if (g > 250) {
				g2 = g - subG
			}
			if (b > 250) {
				b2 = b - subB
			}
			if (r < 50) {
				r2 = r + 3 * subR
			}
			if (g < 50) {
				g2 = g + 3 * subG
			}
			if (b < 50) {
				b2 = b + 3 * subB
			}
			// //
			// let subR = 40
			// let subG = 30
			// let subB = 42
			// let r2 = (r < 125) ? (r + subR) : (r - subR)
			// let g2 = (g < 255) ? (g + subG) : (g - subG)
			// let b2 = (b < 255) ? (b + subB) : (b - subB)


			// console.log("R2:" + r2 + " G2:" + g2 + " B2:" + b2);
			// 1016
			this.model.colorHeader = "#" + r2.toString(16) + g2.toString(16) + b2.toString(16)
		},
		// 
		// v-model:xxx @update:xxx  https://www.naiveui.com/zh-CN/os-theme/docs/common-issues
		saveConfigToLocalStorage() {
			// 
			localStorage.setItem("showHeaderFlag", this.showHeaderFlag);
			localStorage.setItem("showPageNumFlag_ScrollMode", this.showPageNumFlag_ScrollMode);
			localStorage.setItem("imageWidth_usePercentFlag", this.imageWidth_usePercentFlag);
			localStorage.setItem("singlePageWidth_Percent", this.singlePageWidth_Percent);
			localStorage.setItem("doublePageWidth_Percent", this.doublePageWidth_Percent);
			localStorage.setItem("singlePageWidth_PX", this.singlePageWidth_PX);
			localStorage.setItem("doublePageWidth_PX", this.doublePageWidth_PX);
			localStorage.setItem("ScrollModeDefaultColor", this.model.color);
			//setsetXXXChangeset
			localStorage.setItem("ImageParameters_DoAutoCrop", this.imageParameters.auto_crop);
			localStorage.setItem("ImageParametersResizeMaxWidth", this.imageParameters.resize_max_width);
		},
		setShowHeaderChange(value) {
			// console.log("value:" + value);
			this.showHeaderFlag = value;
			localStorage.setItem("showHeaderFlag", value);
			console.log(": showHeaderFlag=" + localStorage.getItem("showHeaderFlag"));
		},
		setShowPageNumChange(value) {
			// console.log("value:" + value);
			this.showPageNumFlag_ScrollMode = value;
			localStorage.setItem("showPageNumFlag_ScrollMode", value);
			console.log(": showPageNumFlag_ScrollMode=" + localStorage.getItem("showPageNumFlag_ScrollMode"));
		},
		setImageParameters_Gray(value) {
			// console.log("value:" + value);
			this.imageParameters.gray = value;
			localStorage.setItem("ImageParameters_Gray", value);
			console.log(": ImageParameters_Gray=" + localStorage.getItem("ImageParameters_Gray"));
		},

		setImageParameters_DoAutoResize(value) {
			this.imageParameters.do_auto_resize = value;
			localStorage.setItem("ImageParameters_DoAutoResize", value);
			console.log(": ImageParameters_DoAutoResize=" + localStorage.getItem("ImageParameters_DoAutoResize"));
		},

		setImageParameters_DoAutoCrop(value) {
			this.imageParameters.do_auto_crop = value;
			localStorage.setItem("ImageParameters_DoAutoCrop", this.imageParameters.do_auto_crop);
			console.log(": ImageParameters_DoAutoCrop=" + localStorage.getItem("ImageParameters_DoAutoCrop"));
		},

		setImageWidthUsePercentFlag(value) {
			console.log("value:" + value);
			this.imageWidth_usePercentFlag = value;
			localStorage.setItem("imageWidth_usePercentFlag", value);
			console.log(": imageWidth_usePercentFlag=" + this.imageWidth_usePercentFlag);
		},

		//
		onResize() {
			this.imageMaxWidth = window.innerWidth
			// document.querySelectorAll(".name");
			this.clientWidth = document.documentElement.clientWidth
			this.clientHeight = document.documentElement.clientHeight
			// var aspectRatio = window.innerWidth / window.innerHeight
			this.aspectRatio = this.clientWidth / this.clientHeight
			//console.log("OnReSize,aspectRatio=" + this.aspectRatio);
			// 
			if (this.aspectRatio > (19 / 19)) {
				this.isLandscapeMode = true
				this.isPortraitMode = false
			} else {
				this.isLandscapeMode = false
				this.isPortraitMode = true
			}
		},
		//
		onScroll() {
			let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
			this.scrollDownFlag = scrollTop > this.scrollTopSave;
			//
			let step = Math.abs(this.scrollTopSave - scrollTop);
			// console.log("step:", step);
			this.scrollTopSave = scrollTop
			if (step > 5) {
				this.showBackTopFlag = (scrollTop > 400) && !this.scrollDownFlag;
			}
		},
		//
		onMouseClick(e) {
			this.clickX = e.x //Xpx
			this.clickY = e.y //Ypx
			//:
			let innerWidth = window.innerWidth
			let innerHeight = window.innerHeight
			let MinX = innerWidth * 0.4
			let MaxX = innerWidth * 0.6
			let MinY = innerHeight * 0.4
			let MaxY = innerHeight * 0.6
			if ((this.clickX > MinX && this.clickX < MaxX) && (this.clickY > MinY && this.clickY < MaxY)) {
				//alert("")
				//console.log("");
				this.drawerActivate('right')
			}
			// console.log("window.innerWidth=", window.innerWidth, "window.innerHeight=", window.innerHeight);
			// console.log("MinX=", MinX, "MaxX=", MaxX);
			// console.log("MinY=", MinY, "MaxY=", MaxY);
			// console.log("x=", e.x, "y=", e.y);
		},

		onMouseMove(e) {
			this.clickX = e.x //Xpx
			this.clickY = e.y //Ypx
			//:
			let innerWidth = window.innerWidth
			let innerHeight = window.innerHeight
			let MinX = innerWidth * 0.4
			let MaxX = innerWidth * 0.6
			let MinY = innerHeight * 0.4
			let MaxY = innerHeight * 0.6
			if ((this.clickX > MinX && this.clickX < MaxX) && (this.clickY > MinY && this.clickY < MaxY)) {
				//console.log("");
				e.currentTarget.style.cursor = 'url(/images/SettingsOutline.png), pointer';
			} else {
				e.currentTarget.style.cursor = '';
			}
		},
		onMouseLeave(e) {
			//
			e.currentTarget.style.cursor = '';
		},

		scrollToTop(scrollDuration) {
			let scrollStep = -window.scrollY / (scrollDuration / 15),
				scrollInterval = setInterval(function () {
					if (window.scrollY !== 0) {
						window.scrollBy(0, scrollStep);
					}
					else clearInterval(scrollInterval);
				}, 15);
		},
		//(viewport)
		// aspect-ratio https://developer.mozilla.org/zh-CN/docs/Web/CSS/@media/aspect-ratio
		// window.innerWidth  
		inLandscapeModeCheck() {
			//0
			// if (window.innerHeight == 0) {
			// 	return false
			// }
			// var aspectRatio = document.documentElement.clientWidth / document.documentElement.clientHeight
			this.aspectRatio = window.innerWidth / window.innerHeight
			// console.log("aspectRatio=" + this.aspectRatio);
			// 
			return this.aspectRatio > (19 / 19);
		},
	},

	computed: {
		sPWL() {
			if (this.imageWidth_usePercentFlag) {
				return this.singlePageWidth_Percent + '%';
			} else {
				return this.singlePageWidth_PX + 'px';
			}
		},
		dPWL() {
			if (this.imageWidth_usePercentFlag) {
				return this.doublePageWidth_Percent + '%';
			} else {
				return this.doublePageWidth_PX + 'px';
			}
		},
		sPWP() {
			if (this.imageWidth_usePercentFlag) {
				return this.singlePageWidth_Percent + '%';
			} else {
				return this.singlePageWidth_PX + 'px';
			}
		},
		dPWP() {
			if (this.imageWidth_usePercentFlag) {
				return this.doublePageWidth_Percent + '%';
			} else {
				return this.doublePageWidth_PX + 'px';
			}
		},
	}
});
</script>

